.formTodo {
    display: flex;
    gap: 10px;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

.inputFormTodo {
    padding: 10px 20px;
    margin: 10px;
    border-radius: 10px;
    min-width: 200px;
    font-size: 1.1rem;
}

.inputFormTodo:focus-visible {
    box-shadow: 0px 0px 15px 2px #7764b2;
}

.buttonFormTodo  {
    padding: 10px 20px;
    margin: 10px;
    border-radius: 10px;
    background-color: rgb(211, 211, 211);
    font-size: 1.1rem;
    cursor: pointer;
}

.buttonFormTodo:hover  {
    background-color:  #b0a6d0;
}

.buttonFormTodo:active  {
    box-shadow: 0px 0px 8px 1px #7764b2;
    background-color: #b0a6d0;;
}