.todo {
    margin-left: 10px;
    font-size: 1.2rem; 
    white-space: normal;
    width: 70%;
    flex-grow: 1;
}

.todoIconFill {
    color: #076314;
    font-size: 1.8rem;
}

.todoIconDel {
    color: #999;
    font-size: 1.5rem;
    margin-right: 15px;
    cursor: pointer;
    transition: transform 0.3s;
}

.todoIconDel:hover {
    color: #920808;
    transform: scale(1.2);
}

.todoIconCheck {
    color: #999;
    font-size: 1.5rem;
    cursor: pointer;
    transition: transform 0.3s;
}

.todoIconCheck:hover {
    color: #076314;
    transform: scale(1.2);
}


.todoWrap {
    color: black;
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(211, 211, 211);
    margin: 20px auto;
    padding: 15px;
    text-align: left;
    border-radius: 5px;
    justify-content: space-between;
    border: 1px solid #fff;
}

.todoWrap.completedTodo .todoIconFill, .todoWrap.completedTodo .todoIconDel, .todoWrap.completedTodo .todoIconCheck{
    color: #666;
}

.completedTodo {
    background: unset;
    border: 1px solid #666;
    color: #666;
}

@media only screen and (max-width: 600px)  {
    .todo {
        font-size: 1rem; 
    }
    .todoIconFill, .todoIconDel, .todoIconCheck {
        font-size: 1.2rem;
    }
}

@media only screen and (max-width: 300px)  {
    .todo {
        width: 60%;
    }
}